import React from "react";
import {NavLink} from "react-router-dom";

const Navbar = () => {
  return (
    <>
      <div className="Container-fluid">
          <div className="col-10 mx-auto">
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
              <div className="container-fluid">
                <NavLink exact className="navbar-brand" to="/">
                  Danish Kaushal
                </NavLink>
                <button
                  className="navbar-toggler"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div
                  className="collapse navbar-collapse"
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                      <NavLink exact activeClassName="nav_active"
                        className="nav-link active"
                        aria-current="page"
                        to="/"
                      >
                        Home
                      </NavLink>
                    </li>
                    <li className="nav-item">
                      <NavLink exact activeClassName="nav_active" className="nav-link" to="/About">
                        About
                      </NavLink> 
                    </li>

                    <li className="nav-item">
                      <NavLink exact activeClassName="nav_active" className="nav-link" to="/Service">
                        Services
                      </NavLink>
                    </li>

                    <li className="nav-item">
                      <NavLink exact activeClassName="nav_active" className="nav-link" to="/Contact">
                        Contact
                      </NavLink>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
          </div>
      </div>
    </>
  );
};
export default Navbar;
